.short-search {
  display: flex;

  &__button {
    flex-shrink: 0;
    margin-right: 25px;
    padding: 0;
    border: 0;
    background: none;
    appearance: none;
    cursor: pointer;
  }

  &__input {
    width: 100%;
    padding: 0;
    border: 0;
    appearance: none;
  }
}